/* Variables */
html {
  --light-base: #e0e5ec;
  --grey: #e7e7e7;
  --lm-light-shadow: #fff;
  --lm-dark-shadow: #a3b1c6;
  --text-shadow: 2px 2px 0 rgba(0,0,0,0.2);
  --box-shadow: 0 0 5px 5px rgba(0,0,0,0.2);
  font-size: 62.5%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  box-sizing: border-box;
}

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background-color: var(--light-base);
}

.shopping {
  display: flex;
  justify-content: center;
}

.shopping-list {
  padding: 20px;
  max-width: 600px;
  width: 100%;
  min-height: 200px;
	border-radius: 12px;
	box-shadow:
		-9px -9px 16px 0px var(--lm-light-shadow),
		9px 9px 16px 0px var(--lm-dark-shadow);
}

.shopping-list ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.shopping-item {
  padding: 1rem 0;
  display: grid;
  grid-template-columns: auto 1fr auto;
  grid-gap: 1rem;
  align-items: center;
  border-bottom: 1px solid var(--gray);

  input[type="checkbox"] { margin-right: 1rem; }
  input[type="checkbox"]:checked + .itemName { opacity: 0.5; }

  button {
    padding: 0;
    font-size: 1rem;
    cursor: pointer;
    transition: transform 0.2s;

    &:hover {
      transform: scale(1.4);
    }
  }
}
